
.content{
    padding: 5px;
}

section.card_facture{
    width: 40%;  
    min-width: 400px;
    max-width: 500px;
    overflow: hidden;
    transition: .3s;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.receipt-container{
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}
.card_facture.showSectionCard{
    min-width: 0;
    width: 0;
}
.factureHeader{
    background-color: #01414a;
    color: white;
    height: var(--hight);
}


.bill-container{
    width: 100%;
    background: #fff;
    color: black;
    //max-width: 350px;
    padding: 25px;
    margin: 50px auto 0;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    overflow-y: scroll;
    overflow-x: hidden;
    .receipt_header {
        padding-bottom: 40px;
        border-bottom: 1px dashed #000;
        text-align: center;
    }
    
    .receipt_header h1 {
        font-size: 20px;
        margin-bottom: 5px;
        text-transform: uppercase;
    }
    
    .receipt_header h1 span {
        display: block;
        font-size: 25px;
    }
    
    .receipt_header h2 {
        font-size: 14px;
        color: #727070;
        font-weight: 300;
    }
    
    .receipt_header h2 span {
        display: block;
    }
    
    .receipt_body {
        margin-top: 25px;
    }
    
    table {
        width: 100%;
    }
    
    thead, tfoot {
        position: relative;
    }
    
    thead th:not(:last-child) {
        text-align: left;
    }
    
    thead th:last-child {
        text-align: right;
    }
    
    thead::after {
        content: '';
        width: 100%;
        border-bottom: 1px dashed #000;
        display: block;
        position: absolute;
    }
    
    tbody td:not(:last-child), tfoot td:not(:last-child) {
        text-align: left;
    }
    
    tbody td:last-child, tfoot td:last-child{
        text-align: right;
    }
    
    tbody tr:first-child td {
        padding-top: 15px;
    }
    
    tbody tr:last-child td {
        padding-bottom: 15px;
    }
    
    tfoot tr:first-child td {
        padding-top: 15px;
    }
    
    tfoot::before {
        content: '';
        width: 100%;
        border-top: 1px dashed #000;
        display: block;
        position: absolute;
    }
    
    tfoot tr:first-child td:first-child, tfoot tr:first-child td:last-child {
        font-weight: bold;
        font-size: 20px;
    }
    
    .date_time_con {
        display: flex;
        justify-content: center;
        column-gap: 25px;
    }
    
    .items {
        margin-top: 25px;
    }
    
    h3 {
        border-top: 1px dashed #000;
        padding-top: 10px;
        margin-top: 25px;
        text-align: center;
        text-transform: uppercase;
    }
}

#pos-container ul li:not(.list-item){
    list-style: none;
    height: 50px;
    width: calc((100%/5) - 2px);
    background-color: var(--blue);
}

.factureNav{
    cursor: pointer;
}


.nameLogicielCard{
    text-align: center;
}
.nameLogicielCard h1{
    font-size: 30px;
}
.nameLogicielCard h1 span{
    color: #e68501;
}
.nameLogicielCard p{
    font-size: 12px;
}




















